import * as React from "react";
import ServicesBar from "../../components/Services";
import Navbar from "../../components/Header/Navbar";
import { Box, Paper, Typography } from "@mui/material";
import { styled, useTheme } from "@mui/material/styles";
import { Client } from "../../globals";
import Reviews from "./sections/Reviews";

const Image = styled(Box)(({ theme, src }) => ({
  width: "100%",
  backgroundImage: src ? `url(${src})` : null,
  backgroundRepeat: "no-repeat",
  backgroundPosition: "center",
  backgroundSize: "cover"
}));

export default function Services({ name }) {
  const defaultServiceName = "implantologia";
  const activeName = name || defaultServiceName;
  const theme = useTheme();

  const { services } = Client;
  const activeService = !activeName
    ? null
    : services.find(({ name }) => name === activeName);

  return (
    <>
      <Box sx={{ color: "secondary.main" }}>
        <Navbar />
        <ServicesBar active={activeName} title="" />
      </Box>

      {/* SERVICE CARD */}
      <Paper
        sx={{
          mt: 3,
          p: 4,
          gap: 3,
          backgroundColor: "primary.soft",
          overflow: "hidden",
          display: "flex",
          textAlign: "center",
          justifyContent: "space-evenly",
          flexDirection: "column",
          [theme.breakpoints.up("xl")]: {
            flexDirection: "row"
          }
        }}
      >
        <Box
          sx={{
            flex: "3",
            minHeight: "500px",
            minWidth: "250px",
            overflow: "hidden",
            display: "flex",
            justifyContent: "center"
          }}
        >
          <Image
            component="div"
            title={activeService?.name}
            sx={{
              borderRadius: "2rem"
            }}
            {...(activeService?.img || {})}
          />
        </Box>
        <Box sx={{ flex: "5" }}>
          <Typography variant="h4">{activeService?.label}</Typography>
          <Typography component="section" id="trattamento" sx={{ textAlign: "justify" }}>
            {activeService?.description}
          </Typography>
        </Box>
      </Paper>

      {/* REVIEWS CARDS */}
      <Reviews />
    </>
  );
}
